import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { IFunctionDefinition, IMapKeyValue, IOptionsPsContainerLookupOptionComponentExposed } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsTemplateListPage } from 'src/app/commonSRC/psTemplates/ps-template-list/ps-template-list.template';
import { CommonBussinessConstant } from '../../psServices/models/ps-common-bussiness-constant';
import { PsCommonBusinessSettings } from '../../psServices/models/ps-commonBusiness.settings';
import { IOptionsPsFavPayeeListExposed } from './ps-fav-payee-list.component.interfaces';




/**
 * @author Ammar.Ahmed
 * @since 22/06/2021
 *
 * <p> PsInternalBeneficiaryComponent is business component contains mapping for for container-lookup component to load internal beneficiaries</p>
 */
@Component({
  selector: 'ps-fav-payee-list',
  templateUrl: './ps-fav-payee-list.component.html',
  styleUrls: ['./ps-fav-payee-list.component.scss'],
})
export class PsFavPayeeListComponent extends PsTemplateListPage implements OnInit {
  @Output() reloadFct = new EventEmitter<string>();
  @Input() options: IOptionsPsFavPayeeListExposed = {};
  // tslint:disable-next-line: no-output-on-prefix
  @Output() public onClickCard = new EventEmitter<any>();
  public labelValuesMap = new Map<string, IMapKeyValue>();
  public headerMap = new Map<string, IMapKeyValue>();
  public requestMap = new Map<string, string>();
  public editRequestMap = new Map<string, string>();
  updateList: any[];
  bRefresh: any = false;
  postFct: IFunctionDefinition = {
    func() {
      return new Promise<any>((resolve, reject) => {
        resolve(this.executionClass.reloadList());
      });
    },
    executionClass: this,
    params: []
  };
  constructor(
  ) {
    super();
  }


  ngOnInit() {
    super.init();
    this.pupulateFavPayeeMap();
    if (this.options.showOnlyList === null || this.options.showOnlyList === undefined) {
      this.options.showOnlyList = false;
    }
    if (this.options && this.options.listOfOptions) {
      this.options.listOfOptions.map((element, index) => element['rowIndex'] = index);
      this.options.itemList = this.options.listOfOptions;
    }
  }

  getContainerLookUpOptions() {
    return this.prepareLookUp();
  }

  private prepareLookUp(): any {
    let lookupDetails: IOptionsPsContainerLookupOptionComponentExposed = {};
    lookupDetails.labelKey = this.options.labelKey;
    lookupDetails.fcName = this.options.fcName;
    lookupDetails.labelsValueMap = this.labelValuesMap;
    lookupDetails.headerMap = this.headerMap;
    lookupDetails.formGroup = this.options.group;
    lookupDetails.isEditable = this.options.isEditable;
    lookupDetails.itemList = this.options.itemList;


    lookupDetails.actionDetailsOptions = [{
      id: 'pay',
      label: 'pay_key',
      actionHyperlink: {
        group: this.options.group,
        iconOptions: {
          iconName: 'paper-plane',
          labelOptions: {
            labelKey: 'pay_key'
          }
        },
        pageOptions: {
          operId: CommonBussinessConstant.WIDGET_BILL_PAYMENTS_OPER_ID,
        },
        navigationOptions: {
          queryParams: { navigationIndex: 1 }
        }
      }
    }, {
      id: 'delete',
      labelOptions: {
        labelKey: 'delete_key'
      },
      buttonIcon: 'delete',
      actionUrl: PsCommonBusinessSettings.serviceUrl.deleteBeneficiaryEndPoint,
      cssClass: 'fab-edit-button',
      label: 'delete_key',
      iconName: 'trash',
      actionType: 'delete',
      alertStyle: 'warning',
      alertType: 'prompt',
      alertMessage: 'confirm_delete_this_favorite_key',
      operId: CommonBussinessConstant.DELETE_FAV_PAYEE_OPER_ID
    }, {
      id: 'edit',
      label: 'edit_key',
      actionHyperlink: {
        group: this.options.group,
        iconOptions: {
          iconName: 'paper',
          labelOptions: {
            labelKey: 'edit_key'
          }
        },
        pageOptions: {
          operId: CommonBussinessConstant.EDIT_FAV_PAYEE_OPER_ID,
        }
      }
    }];
    lookupDetails.statementOptions = {};
    lookupDetails.reloadFunction = {
      func() {
        return new Promise<any>((resolve, reject) => {
          resolve(this.executionClass.reloadList());
        });
      },
      executionClass: this,
      params: []
    }
    return lookupDetails;
  }

  reloadList() {
    this.reloadFct.emit();
  }

  private pupulateFavPayeeMap() {
    let billerMap: IMapKeyValue = {};
    let categoryMap: IMapKeyValue = {};
    let referenceMap: IMapKeyValue = {};
    let headerTitleMap: IMapKeyValue = {};

    billerMap = {
      key: 'billerDescriptionConcat',
      value: '',
      isEdit: false,
    };

    categoryMap = {
      key: 'categoryDescriptionConcat',
      value: '',
      isEdit: false,
    };

    referenceMap = {
      key: 'billerAccount',
      value: '',
      isEdit: false
    };

    headerTitleMap = {
      key: 'title',
      value: 'nickName',
      isEdit: false,
    };

    // key here will be used as label in look up details component
    this.labelValuesMap.set('biller_key', billerMap);
    this.labelValuesMap.set('category_key', categoryMap);
    this.labelValuesMap.set('reference_key', referenceMap);
    this.headerMap.set('title', headerTitleMap);
  }

  public updateCard(item: any) {
    if (item && this.options.listOfOptions) {
      this.options.itemList = this.options.listOfOptions.filter(acct => acct === item);
      this.options.item = this.options.itemList[0];
      this.options.showItemPopUp = !this.options.showItemPopUp;
    }
    this.onClickCard.emit(this.options);
  }

}
